import ActionType from '../constants/actionTypes';
import config from '../config';

export function actionResetPassword(status, message) {
  return {
    type: ActionType.RESET_PASSWORD,
    status,
    message
  };
}
export default function fetchResetPassword(token, password) {
  return dispatch => {
    dispatch(actionResetPassword('REQUEST', 'Xin vui lng i...'));

    return fetch(config['server-domain'] + 'users/password/reset', {
      method: 'POST',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        token: token,
        newPassword: password
      })
    })
      .then(
        response => response.json(),
        error => {
          console.log('An error occurred.', error);
          dispatch(
            actionResetPassword('FAILED', ' c li xy ra, vui lng th li')
          );
        }
      )
      .then(json => {
        dispatch(actionResetPassword('SUCCESS', json.message));
      })
      .catch(err => {
        dispatch(
          actionResetPassword('FAILED', ' xy ra li, xin vui lng th li')
        );
      });
  };
}
