import React from 'react';
import Config from '../../constants/configs';
import { connect } from 'react-redux';
import fetchRecord from '../../actions/actionRecordResult'
import { bindActionCreators } from 'redux';

function Status(props) {
    const {actions} = props;
    const { winCells } = props;
    const { rivalname } = props;
    const { isPlayerX } = props;
    const { messages } = props;
    const {playWithAI} = props;
    const {username} = props;
    const {isTimeOut} = props;
    //const {handleEnd} = props;
    let message;

    if (rivalname === 'DISCONNECTED') {
        message = 'i th  thot khi phng chi !';
    }
    else if (messages) {
           message = messages;
         } else if (isTimeOut && !winCells) {
           const winner =
             props.nextMove === Config.xPlayer
               ? Config.xPlayer
               : Config.oPlayer;

           if (
             (isPlayerX && winner === Config.oPlayer) ||
             (!isPlayerX && winner === Config.xPlayer)
           ) {
             if (!playWithAI) {
               actions.fetchRecord(username, 'win');
             }
             message = `Chc mng bn  ginh chin thng !`;
           } else {
             if (!playWithAI) {
               actions.fetchRecord(username, 'lose');
             }
             message = `Rt tic bn  thua cuc !`;
           }
         } else if (winCells) {
           //handleEnd();
           const winner =
             props.nextMove === Config.xPlayer
               ? Config.oPlayer
               : Config.xPlayer;
           if (
             (isPlayerX && winner === Config.oPlayer) ||
             (!isPlayerX && winner === Config.xPlayer)
           ) {
             if (!playWithAI) {
               actions.fetchRecord(username, 'lose');
             }
             message = `Rt tic bn  thua cuc !`;
           } else {
             if (!playWithAI) {
               actions.fetchRecord(username, 'win');
             }
             message = `Chc mng bn  ginh chin thng !`;
           }
         } else {
           message = `Lt i k tip: ${props.nextMove}`;
         }
    return (
        <div className='status'><b>{message}</b></div>
    )
}
function mapStateToProps(state) {
    return {userInfo: state.infoReducers.userInfo}
}
function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({
            fetchRecord,
        }, dispatch)
    };
}
export default connect(mapStateToProps, mapDispatchToProps)(Status);