import ActionType from '../constants/actionTypes';
import config from '../config';

export function actionActive(status, message) {
  return {
    type: ActionType.ACTIVE,
    status,
    message
  };
}
export default function fetchActive(token) {
  return dispatch => {
    dispatch(actionActive('REQUEST', 'Xin vui lng i...'));

    return fetch(config['server-domain'] + 'users/activate', {
      method: 'POST',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        token: token
      })
    })
      .then(
        response => response.json(),
        error => {
          console.log('An error occurred.', error);
          dispatch(
            actionActive('FAILED', ' c li xy ra, vui lng th li')
          );
        }
      )
      .then(json => {
        dispatch(actionActive('SUCCESS', json.message));
      })
      .catch(err => {
        dispatch(actionActive('FAILED', ' xy ra li, xin vui lng th li'));
      });
  };
}
